img {
  max-width: 100%;
  height: auto;
}
.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}
@font-face {
  font-family: "Oswald";
  font-weight: 400;
  font-style: normal;
  src: url("../../fonts/Oswald-Regular.woff2") format("woff2"), url("../../fonts/Oswald-Regular.woff") format("woff");
}
.page {
  height: 100%;
  background-color: #FFFFFF;
}
.page__body {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin: 0;
  min-height: 100%;
  min-width: 320px;
  font-size: 18px;
  font-family: "Oswald", "Arial", sans-serif;
  line-height: 27px;
  color: #111111;
  background-color: #FFFFFF;
}
.page-header {
  position: relative;
  background-color: #FFFFFF;
  width: 100%;
  min-width: 320px;
  max-width: 1440px;
}
@media (min-width: 1440px) {
  .page-header {
    display: flex;
    flex-wrap: wrap;
    background-color: initial;
  }
}
.header__container {
  display: flex;
  justify-content: space-between;
  align-items: center;
  min-height: 66px;
  margin: 0 20px;
}
@media (min-width: 768px) {
  .header__container {
    min-height: 94px;
    margin: 0 30px;
  }
}
@media (min-width: 1440px) {
  .header__container {
    align-items: flex-end;
    width: 610px;
    min-height: 112px;
    margin: 0;
    margin-left: 110px;
  }
}
.header__logo {
  min-width: 34px;
  height: 38px;
}
@media (min-width: 768px) {
  .header__logo {
    min-width: 174px;
    min-height: 52px;
  }
}
@media (min-width: 1440px) {
  .header__logo {
    min-width: 202px;
    min-height: 59px;
    margin-bottom: 15px;
  }
}
.header__logo-text {
  min-width: 101px;
  height: 18px;
  margin-top: -3px;
}
@media (min-width: 768px) {
  .header__logo-text {
    display: none;
  }
}
.header__toggle-container {
  width: 24px;
  height: 24px;
  margin-left: 10px;
  cursor: pointer;
}
@media (min-width: 768px) {
  .header__toggle-container {
    width: 36px;
    height: 36px;
    margin-left: 0;
  }
}
@media (min-width: 1440px) {
  .header__toggle-container {
    display: none;
  }
}
.header__toggle {
  display: block;
  position: relative;
  top: 3px;
  width: 24px;
  height: 1.5px;
  margin-right: 30px;
  background-color: #111111;
  cursor: pointer;
  border: none;
  transition: all 0.5s ease 0s;
}
@media (min-width: 768px) {
  .header__toggle {
    top: 7px;
    width: 36px;
    height: 2px;
    z-index: 3;
  }
}
.header__toggle::before {
  content: "";
  position: absolute;
  top: 8px;
  left: 0px;
  width: 24px;
  height: 1.5px;
  background-color: #111111;
  cursor: pointer;
  border: none;
  transition: all 0.5s ease 0s;
}
@media (min-width: 768px) {
  .header__toggle::before {
    top: 10px;
    width: 36px;
    height: 2px;
  }
}
.header__toggle::after {
  content: "";
  position: absolute;
  top: 16px;
  left: 0px;
  width: 24px;
  height: 1.5px;
  background-color: #111111;
  cursor: pointer;
  border: none;
  transition: all 0.5s ease 0s;
}
@media (min-width: 768px) {
  .header__toggle::after {
    top: 20px;
    width: 36px;
    height: 2px;
  }
}
.header__toggle--closed {
  position: relative;
  left: 5px;
  top: 3px;
  background-color: #FFFFFF;
}
@media (min-width: 768px) {
  .header__toggle--closed {
    z-index: 3;
    background-color: #68B738;
  }
}
.header__toggle--closed::before,
.header__toggle--closed::after {
  content: "";
  position: absolute;
  top: 8px;
  left: -2px;
  width: 24px;
  height: 1.5px;
  background-color: #111111;
  border-radius: 1.5px;
  transform: matrix(0.71, -0.73, 0.69, 0.71, 0, 0);
}
@media (min-width: 768px) {
  .header__toggle--closed::before,
  .header__toggle--closed::after {
    top: 14px;
    left: -2px;
    width: 36px;
    height: 2px;
    background-color: #FFFFFF;
  }
}
.header__toggle--closed::before {
  transform: rotate(45deg);
  box-shadow: none;
}
.header__toggle--closed::after {
  transform: rotate(-45deg);
}
.header__nav-list {
  display: flex;
  flex-direction: column;
  list-style: none;
  margin: 0;
  padding: 0;
}
@media (min-width: 768px) {
  .header__nav-list {
    position: absolute;
    width: 350px;
    top: 11px;
    right: 11px;
    z-index: 2;
    background-color: #68B738;
    border: 1px solid #E6E6E6;
    border-top: none;
    border-radius: 5px;
    box-shadow: 0 12px 24px #ADFF2F, 0 10px 14px #E6E6E6;
  }
}
@media (min-width: 1440px) {
  .header__nav-list {
    position: relative;
    flex-direction: row;
    justify-content: flex-end;
    align-items: flex-end;
    width: 610px;
    background-color: initial;
    padding: 0 95px 0 15px;
    border: none;
    border-radius: 0;
    top: 0;
    right: 0;
    box-shadow: none;
  }
}
@media (max-width: 1439px) {
  .header__nav-list--closed {
    position: absolute;
    width: 1px;
    height: 1px;
    margin: -1px;
    border: 0;
    padding: 0;
    white-space: nowrap;
    clip-path: inset(100%);
    clip: rect(0 0 0 0);
    overflow: hidden;
  }
}
.header__nav-item {
  min-height: 66px;
  margin: 0;
  padding: 0;
  border-top: 1px solid #E6E6E6;
}
@media (min-width: 1440px) {
  .header__nav-item {
    border: none;
  }
}
@media (min-width: 1440px) {
  .header__nav-item {
    margin: 0 25px;
  }
}
.nav-item__link {
  display: flex;
  justify-content: center;
  align-items: center;
  text-decoration: none;
  width: auto;
  min-height: 66px;
  font-family: "Oswald", "Arial", sans-serif;
  font-style: normal;
  font-weight: 500;
  font-size: 20px;
  line-height: 20px;
  text-transform: uppercase;
  color: #111111;
}
@media (min-width: 768px) {
  .nav-item__link {
    font-weight: normal;
    font-size: 18px;
    line-height: 24px;
    color: #FFFFFF;
  }
}
@media (min-width: 1440px) {
  .nav-item__link {
    justify-content: center;
    align-items: flex-start;
    width: min-content;
    min-height: 24px;
  }
}
.main__banner {
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 100vw;
  min-width: 320px;
  max-width: 1440px;
  background-image: url(../img/background-mobile.svg), url(../img/index-background-mobile.png);
  background-repeat: no-repeat, no-repeat;
  background-size: 100% auto, 100% auto;
  background-position: top center, top center;
}
@media (min-width: 768px) {
  .main__banner {
    align-items: flex-start;
  }
}
@media (min-width: 1440px) {
  .main__banner {
    position: relative;
    background-image: url(../img/background-mobile.svg), url(../img/index-background-desktop.png);
    background-size: 50% auto, 50% auto;
    background-position: 100% 0, 100% -15px;
    margin-top: -112px;
    min-height: 694px;
  }
}
.banner__heading {
  width: 249px;
  min-height: 72px;
  margin: 27px 0 0 0;
  font-family: "Oswald", "Arial", sans-serif;
  font-style: normal;
  font-weight: normal;
  font-size: 36px;
  line-height: 36px;
  text-align: center;
  color: #FFFFFF;
}
@media (min-width: 768px) {
  .banner__heading {
    width: 60vw;
    min-height: 120px;
    margin: 62px 0 0 117px;
    font-size: 60px;
    line-height: 60px;
    text-align: left;
  }
}
@media (min-width: 1440px) {
  .banner__heading {
    width: 430px;
    min-height: 120px;
    margin: 223px 0 0 190px;
    color: #111111;
  }
}
.banner__paragraph {
