  min-height: 15px;
  margin: 25px 0 0 0;
  font-family: "Oswald", "Arial", sans-serif;
  font-style: normal;
  font-weight: normal;
  font-size: 14px;
  line-height: 14px;
  text-align: center;
  color: #FFFFFF;
  text-transform: uppercase;
}
@media (min-width: 768px) {
  .banner__paragraph {
    width: 300px;
    min-height: 20px;
    margin: 41px 0 0 117px;
    font-size: 20px;
    line-height: 20px;
    text-align: left;
    text-transform: uppercase;
  }
}
@media (min-width: 1440px) {
  .banner__paragraph {
    width: 285px;
    min-height: 20px;
    margin: 40px 0 0 190px;
