  }
}
.banner__button {
  display: none;
}
@media (min-width: 1440px) {
  .banner__button {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 244px;
    min-height: 54px;
    margin: 52px 0 0 190px;
    background-color: #68B738;
  }
}
.banner__button-link {
  font-family: "Oswald", "Arial", sans-serif;
  font-style: normal;
  font-weight: normal;
  font-size: 20px;
  line-height: 20px;
  text-transform: uppercase;
